<template>
  <div style="height: 100%">
<!--    -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item ></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
<!--    -->
    <div class="mainInterface">
      <el-card class="searchCard" shadow="never" v-loading ="loading">
        <el-form :model="queryForm" class="el-form" ref="queryForm" inline>
          <el-form-item label="id" prop="userId">
            <el-input v-model="queryForm.productNo" placeholder=""></el-input>
          </el-form-item>
          <el-form-item class="searchButton">
            <el-button class="el-button-search" type="primary" @click="queryOrder()"></el-button>
            <el-button class="el-button-search" @click="resetForm('queryForm')"></el-button>
          </el-form-item>
        </el-form>
      </el-card>
      <el-card class="mainCard">
        <div style="height: 100%">
          <!--        -->
          <el-table :data="userFormData" style="width: 100%;height: 100%" fit highlight-current-row  class="userTable" v-loading="loading">
            <!--          -->
            <el-table-column  label="#" width="70" type="index"></el-table-column>
            <el-table-column prop="username" label="" width="110"></el-table-column>
            <el-table-column prop="password" label="" width="170"></el-table-column>
            <el-table-column prop="email" label=" " width="180"></el-table-column>
            <el-table-column prop="qq" label="QQ" width="120"></el-table-column>
            <el-table-column prop="phone" label="" width="120"></el-table-column>
            <el-table-column prop="end_time" label="" width="170"></el-table-column>
            <!--          -->
            <el-table-column  label="" width="120" header-align="center">
              <template slot-scope="scope">
                <!--              -->
                <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row)"></el-button>
                <!--              -->
                <el-button type="danger" icon="el-icon-delete" size="mini"  @click="deleteDialog(scope.row.username)" ></el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </el-card>
      <!--        -->
      <el-pagination
          @size-change="handleSizeChange"
          @current-change="queryInfo.pageNum"
          :page-sizes="pageSizes"
          :page-size="pageSize"
          :current-page="pageIndex"
          layout="total, sizes, prev, pager, next, jumper"
          :total="totalMes"
          style="margin-left: 350px">
      </el-pagination>

    </div>
    <!--    -->
    <el-dialog
        :visible.sync="editDialogVisible"
        title=""
        width="50%"
        @close="editDialogClosed">
      <!--      -->
      <el-form :model="editStudentForm" :rules="editStudentRules" ref="editStudentFormRef" label-width="100px" class="demo-ruleForm">
        <!--        -->
        <el-form-item label="">
          <el-input v-model="editStudentForm.username" readonly></el-input>
        </el-form-item>
        <!--        -->
        <el-form-item label="" prop="password">
          <el-input v-model="editStudentForm.password" ></el-input>
        </el-form-item>
        <!--     -->
        <el-select v-model="editStudentForm.school" filterable clearable placeholder=""  class="schoolOptions">
          <!-- v-for="item in schoolName" :key='item.id'       -->
          <el-option
              v-for="item in schoolOptions"
              :key="item.value"
              :value="item.value">
          </el-option>
        </el-select>
        <!--        -->
        <el-form-item label="" prop="campus" >
          <el-input v-model="editStudentForm.campus"  ></el-input>
        </el-form-item>
        <!--        -->
        <el-form-item label="" prop="email" >
          <el-input v-model="editStudentForm.email" readonly></el-input>
        </el-form-item>
        <!--        QQ-->
        <el-form-item label="QQ" prop="qq" >
          <el-input v-model="editStudentForm.qq"  ></el-input>
        </el-form-item>
        <!--        -->
        <el-form-item label=":" prop="phone" type="number">
          <el-input v-model="editStudentForm.phone"  ></el-input>
        </el-form-item>
        <!--        -->
        <el-form-item label=":" prop="end_time" type="number">
          <el-input v-model="editStudentForm.end_time"  ></el-input>
        </el-form-item>
      </el-form>
      <!--      -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editUserInfo()"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  // 
  data () {
    // 
    const checkMobile = (rule, value, cb) => {
      const regMobile = /^1(3|4|5|6|7|8|9)\d{9}$/
      if (regMobile.test(value)) {
        //  
        return cb()
      }
      cb(new Error(''))
    }
    // QQ
    const checkQQNumber = (rule, value, cb) => {
      const regQQ = /^[0-9]{5,12}$/
      if (regQQ.test(value)) {
        //  QQ
        return cb()
      }
      cb(new Error('QQ'))
    }
    return {
      // 
      searchUsername: '',
      // 
      queryInfo: {
        pageNum: 1,
        pageSize: 10
      },
      queryForm:{
      },
      // z
      totalMes: 100,
      pageIndex: 1, //
      pageSize: 10, // 
      pageSizes:[5,10,30,60,100,200,400],
      // 
      userFormData: [
      ],
      // 
      editDialogVisible: false,
      // 
      deleteDialogVisible: false,
      // 
      editStudentForm: {
        campus: '',
        email: '',
        end_time: '',
        id: 1,
        password: '',
        phone: '',
        qq: '',
        school: '',
        username: ''
      },
      // 
      editStudentRules: {
        phone: [
          {
            validator: checkMobile,
            message: '',
            trigger: 'change'
          }
        ],
        qq: [
          {
            validator: checkQQNumber,
            message: 'QQ',
            trigger: 'change'
          }
        ],
        password: [
          {
            type: 'string',
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 10,
            max: 17,
            message: '1017'
          }]
      },
      // username
      deleteUsername: '',
      // 
      loading: false,
      // 
      choseSchool: true
    }
  },
  // 
  async created () {
    // 
    await this.getTotalNumber()
    // 
    await this.getAllUserInf()
  },
  // 
  methods: {
    // 
    async getAllUserInf () {
      // 
      this.loading = true
      // 
      const formData = new FormData()
      formData.append('pagenum', this.queryInfo.pageNum)
      formData.append('keyword', this.searchUsername)
      for (var value of formData.values()) { console.log(value) }
      const token = window.sessionStorage.getItem('token')
      const { data: res } = await this.$axios.post('/get_user', formData,
        {
          headers: {
            accesstoken: token
          }
        })
      console.log(res)
      if (!res) {
        this.$message.error('')
      }
      // 
      this.loading = false
      this.userFormData = res
      this.$message.success('')
    },
    // 
    async getTotalNumber () {
      // 
      const formData = new FormData()
      formData.append('keyword', this.searchUsername)
      const token = window.sessionStorage.getItem('token')
      const { data: res } = await this.$axios.post('/get_user_number', formData,
        {
          headers: {
            accesstoken: token
          }
        })
      if (!res) {
        this.$message.error('')
      }
      // 
      this.totalMes = res
    },
    //
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    //
    queryOrder(){

    },
    // 
    async handleSizeChange () {
      console.log(this.queryInfo.pageNum)
      // 
      await this.getTotalNumber()
      await this.getAllUserInf()
    },
    async changeUser () {
      // 
      await this.getTotalNumber()
      await this.getAllUserInf()
    },
    // 
    showEditDialog (row) {
      this.editStudentForm.campus = row.campus
      this.editStudentForm.username = row.username
      this.editStudentForm.qq = row.qq
      this.editStudentForm.school = row.school
      this.editStudentForm.id = row.id
      this.editStudentForm.end_time = row.end_time
      this.editStudentForm.email = row.email
      this.editStudentForm.phone = row.phone
      this.editStudentForm.password = row.password
      this.editDialogVisible = true
    },
    // 
    editDialogClosed () {
      this.$refs.editStudentFormRef.resetFields()
    },
    // 
    async editUserInfo () {
      this.$refs.editStudentFormRef.validate(async valid => {
        if (!valid) return// 
        // 
        const formData = new FormData()
        formData.append('username', this.editStudentForm.username)
        formData.append('qq', this.editStudentForm.qq)
        formData.append('password', this.editStudentForm.password)
        formData.append('phone', this.editStudentForm.phone)
        formData.append('campus', this.editStudentForm.campus)
        formData.append('school', this.editStudentForm.school)
        formData.append('end_time', this.editStudentForm.end_time)
        for (var value of formData.values()) { console.log(value) }
        const token = window.sessionStorage.getItem('token')
        const { data: res } = await this.$axios.post('/user/change', formData,
          {
            headers: {
              accesstoken: token
            }
          })
        console.log(111)
        console.log(res)
        this.$message.success('')
        // 
        await this.getTotalNumber()
        await this.getAllUserInf()
        // 
        this.editDialogVisible = false
      })
    },
    // 
    deleteDialog (username) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.deleteUser(username)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    },
    // 
    async deleteUser (username) {
      // 
      const formData = new FormData()
      formData.append('username', username)
      const token = window.sessionStorage.getItem('token')
      const { data: res } = await this.$axios.post('user/delete', formData,
        {
          headers: {
            accesstoken: token
          }
        })
      console.log(111)
      console.log(res)
      if (!res) {
        this.$message.error('')
      }
      // 
      this.loading = false
      this.$message.success('')
      await this.getTotalNumber()
      await this.getAllUserInf()
    }
  }
}
</script>

<style land="less" scoped>
.userTable{
}
.mainInterface{
  height: 100%;
  padding-top: 1.5%;
}
.mainCard{
  height: 70%;
}
.searchCard{
  height: 12%;
  margin-bottom: 10px;
}
</style>
