//60s
let map = new Map();
const countDown={
  setItem(obj,timer){
    console.log("11")
    timer = timer || 60;  //60s    60
    obj.disabled = true;               //
    let auth_timer = setInterval(()=>{  //
      map.set(obj,auth_timer);
      timer--;        //
      obj.innerHTML= timer + ' ';
      if(timer<=0){
        obj.disabled = false;    //60s 
        clearInterval(auth_timer)
        map.delete(obj)
        obj.innerHTML= '';
      }
    },1000)
  },
  removeItem(obj){
    if(obj){
      let auth_timer =map.get(obj);
      clearInterval(auth_timer);
      obj.innerHTML= '';
      obj.disabled = false;
    }
  },
}

export default countDown;
