<template>
  <div class="register-container">
    <div>
      <!--  -->
      <div class="backgroundPicture">
    <el-form
      ref="registerForm"
      label-width="150px"
      class="register-form"
      :model="registerForm"
      :rules="registerRules"
    >
      <div>
        <img class="register_in_title" src="../assets/image/sdunft.png">
      </div>
      <!--       -->
      <el-form-item prop="username" label=":">
        <el-input v-model="registerForm.username" placeholder="" ></el-input>
      </el-form-item>
      <!--       -->
      <el-form-item prop="email" label=":">
        <el-input v-model="registerForm.email" placeholder=""></el-input>
      </el-form-item>
      <!--       -->
      <el-form-item prop="captcha" label=":">
        <el-input v-model="registerForm.captcha" :disabled="codeRight" style="width: 50%" placeholder=""></el-input>
        <el-button   :type="valBunType" v-preventReClick style="position: absolute;right: 0; width: 40%;top: 0"
                      @click="sendEmailCode('registerForm',$event)" ></el-button>
      </el-form-item>
      <!--       -->
      <el-form-item prop="password" label=":">
        <el-input v-model="registerForm.password" type="password" placeholder="" show-password></el-input>
      </el-form-item>
      <!--       -->
      <el-form-item prop="comparePassword" label=":">
        <el-input v-model="registerForm.comparePassword" type="password" placeholder="" show-password ></el-input>
      </el-form-item>
      <el-button  class="input" type="info" style="margin-top: 20px;width: 80%;margin-left: 20%" native-type="submit" :loading="loading" @click="register" ></el-button>
      <div class="register-info"><el-button :to="{name: 'login'}" type="text" @click="gotoLogin" style="color: #409EFF;font-size: 16px;cursor:hand">
        ></el-button></div>
    </el-form>
    </div>
    </div>
  </div>
</template>

<script>
// eslint-disable-next-line no-unused-vars

export default {
  data: function () {
    return {
      loading: false,
      // 
      centerDialogVisible: false,
      //
      codeButtonTemp:null,
      valDisable:false,
      valBunType:"primary",
      error: '',
      captcha:'',
      codeRight:true,
      registerForm: {
        username: '11111',
        email: '1330220320@qq.com',
        password: '111111',
        comparePassword: '111111',
        captcha:''
      },
      registerRules: {
        username: [
          {
            type: 'string',
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 4,
            max: 12,
            message: '412'
          }],
        email: {
          type: 'email',
          required: true,
          message: '',
          trigger: 'blur'
        },
        captcha:{
          validator: function (rule, value, callback) {
            if (value === '') {
              callback(new Error(''))
            } else {
              callback()
            }
          }.bind(this)
        },
        password: [
          {
            type: 'string',
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 4,
            max: 17,
            message: '417'
          }],
        comparePassword: {
          type: 'string',
          required: true,
          trigger: 'blur',
          validator: function (rule, value, callback) {
            console.log(this.registerForm.password)
            if (value === '') {
              callback(new Error(''))
            } else if (value !== this.registerForm.password) {
              callback(new Error(''))
            } else {
              callback()
            }
          }.bind(this)
        }
      }
    }
  },
  methods: {
    // 
    register: function () {
      // json
      const json = {
        user_id: this.registerForm.username,
        password: this.registerForm.password,
        email_address: this.registerForm.email,
        email_code:this.registerForm.captcha
      };

      this.$refs.registerForm.validate(async valid => {
        // 
        if (valid) {
          const res = await this.$axios({
            url: '/verify',
            method:  'post',
            data: json
          })
          console.log(res)
          // 
          if (res.data.msg === "") {
            return this.$message.success('!')
          } else if (res.data.msg === "") {
            return this.$message.error('!')
          }
        } else {
          return this.$message.error('')
        }
      }
      )
    },
    // 
    gotoLogin() {
      this.$router.push('/login')
    },
    //
    sendEmailCode(formName, event) {
      this.codeButtonTemp = event.currentTarget;
      console.log(this.codeButtonTemp)
      let va = true;
      this.$refs[formName].validateField('email', (valid) => {
        va = valid === "";
      })
      this.$refs[formName].validateField('username', (valid) => {
        va = va && valid === "";
      })
      if (va) {
        const json = {
          email_address: this.registerForm.email,
          user_id: this.registerForm.username
        };
      this.$axios({url: '/register', method: 'post', data: json}).then((res) => {
        console.log(res.status)
        if (res.status === 200) {
            this.codeRight = false;
            this.$countDown.setItem(this.codeButtonTemp);
            if(res.data.msg === "")
              this.$message.warning("");
            else if(res.data.msg === "")
              this.$message.warning("");
            else
              this.$message.success("");
          } else if (res.status === 500) {
            this.$message.warning(res.data.message)
          }
        }).catch((err) => {
          this.$message.error("")
        })
      }
    },
  }
}
</script>

<style lang="less" scoped>
// 
.register-container {
  background-image: url('../assets/image/register.jpg');
  background-size: 100% 100%;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
}
.register_in_title{
  font-size:35px;
  position: center;
  margin-top: 6%;
  padding-left: 10%;
}
.btn-orange{
  margin-left: 10%;
}
// 
.register-form {
  position: absolute;
  width: 35%;
  margin-left: 8%;
  margin-top: 5%;
  background: #0a1123;
  padding: 20px;
  box-sizing: border-box;
  .register-info {
    text-align: right;
    font-size: 0.9rem;
    margin-top: 10px;
    color: #0a1735
  }
  .register-error {
    color: #0a1735
  }
}
</style>
