<template>
  <div class="yourTeam">
    <!--    -->
    <el-breadcrumb separator-class="el-icon-arrow-right" class="breadCrumb">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--    -->
    <el-container class="marketMainContainer">
      <!--      -->
      <el-main class="el-main">
        <!--    ()-->
        <el-backtop :bottom="100">
          <div class="top">Top</div>
        </el-backtop>
        <!--        -->
        <div class="secondCard">
          <!--           -->
          <div class="teamInformation" >
            <el-row>
              <el-col :span="5" v-for="(item, index) in teamInfo"  :offset="1" class="teamCol">
                <el-card  class="teamItem" shadow="hover" style="width: 260px;height: 430px;">
                  <div style="height: 330px;">
                    <!--          -->
                    <el-avatar :src="avatarSrc[0]"></el-avatar>
                    <!--              -->
                    <el-button type="text" icon="el-icon-delete" circle style="padding-left: 34%" v-if="item.die_time > nowDate "
                               @click="deleteTeam(item.team_id)"></el-button>
                    <el-button type="text" icon="el-icon-check" circle style="padding-left: 34%" v-if="item.die_time < nowDate "></el-button>
                    <br>
                    <span>{{' '+item.sponsor+' '}}</span>
                    <br>
                    <span>{{' '+item.title+'   '}}</span>
                    <br>
                    <span>{{' '+item.number+'   '}}</span>
                    <br>
                    <span>{{' '+item.phone+' '}}</span>
                    <br>
                    <span>{{'QQ  '+item.qq+' '}}</span>
                    <br>
                    <span>{{''+item.die_time}}</span>
                    <br>
                    <el-input
                        type="textarea"
                        :rows="5"
                        placeholder=""
                        v-model="item.remark"
                        readonly>
                    </el-input>
                  </div>
                </el-card>
              </el-col>
            </el-row>
          </div>
        </div>
      </el-main>
      <!--      -->
      <el-footer class="el-footer">
        <!--        -->
        <el-button class="teamUpButton" @click="teamUp"></el-button>
        <!--            queryInfo-->
        <el-pagination
            @current-change="handleCurrentChange"
            :current-page.sync="queryInfo.pageNum"
            :page-size="16"
            layout="prev, pager, next, jumper"
            :total="160">
        </el-pagination>
      </el-footer>
    </el-container>
    <!--    -->
    <el-dialog
        title=""
        :visible.sync="postDialogVisible"
        width="35%"
        @close="teamUpDialogClosed"
        class="teamUpDialog">
      <!--      -->
      <!--      -->
      <el-form :model="teamUpForm" :rules="teamUpFormRule" ref="teamUpFormRef" label-width="70px" class="teamUpForm">
        <!--        -->
        <el-form-item label="" prop="title">
          <el-input v-model="teamUpForm.title" ></el-input>
        </el-form-item>
        <!--        -->
        <el-form-item label="/" prop="sponsor" label-width="100px">
          <el-input v-model="teamUpForm.sponsor" ></el-input>
        </el-form-item>
        <!--     -->
        <el-form-item label="" prop="itemType">
          <el-select v-model="teamUpForm.teamType" placeholder="">
            <el-option label="" value=""></el-option>
            <el-option label="happy" value="happy"></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
          </el-select>
        </el-form-item>
        <!--        -->
        <el-form-item label="" prop="number">
          <el-input-number v-model="teamUpForm.number" controls-position="right"  :min="2" :max="100"></el-input-number>
        </el-form-item>
        <!--        -->
        <el-form-item label="" prop="phone">
          <el-input v-model="teamUpForm.phone" ></el-input>
        </el-form-item>
        <!--        qq-->
        <el-form-item label="QQ" prop="qq">
          <el-input v-model="teamUpForm.qq" ></el-input>
        </el-form-item>
        <!--        -->
        <el-form-item label="">
          <el-input type="textarea" v-model="teamUpForm.remarks"
                    maxlength="120"  show-word-limit  autosize></el-input>
        </el-form-item>

      </el-form>
      <!--      -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="postDialogVisible = false"> </el-button>
        <el-button type="primary" @click="postTeamUp"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data (){
    // 
    let checkMobile = (rule,value,cb)=>{
      const regMobile = /^1(3|4|5|6|7|8|9)\d{9}$/
      if (regMobile.test(value) || value === ''){
        //  
        return cb()
      }
      cb(new Error(''))
    }
    // QQ
    let checkQQNumber = (rule,value,cb)=>{
      const regQQ =  /^[0-9]{5,12}$/
      if (regQQ.test(value) || value === ''){
        //  QQ
        return cb()
      }
      cb(new Error('QQ'))
    }
    return {
      nowDate: "", // 
      // 
      queryInfo: {
        keyword: ' ',
        pageNum: 1,
        pageSize: 16
      },
      //  10
      range: '1',
      // ,
      selectAll: true,
      // 
      teamCategory: '',
      // 
      teamInfo:[

      ],
      // 
      loading: false,
      // 
      postDialogVisible: false,
      // 
      teamUpForm:{
        title:'',
        sponsor:'',
        username:'',
        teamType:'',
        number:'',
        phone :'',
        qq :'',
        remarks:'',
        school:'',
      },
      // 
      avatarSrc:[],
      // 
      teamUpFormRule:{
        //
        title: {
          required: true,
          trigger: 'blur',
          min: 2,
          max: 12,
          message: '212'
        },
        ///
        sponsor: {
          required: true,
          trigger: 'blur',
          min: 2,
          max: 12,
          message: '/212'
        },
        // 
        teamType: {
          required: true,
          trigger: 'blur',
        },
        phone: {
          required: false,
          validator: checkMobile,
          trigger: "blur"
        },
        qq: {
          validator: checkQQNumber,
          trigger: 'blur'
        },
      },
    }
  },
  created() {
    // 
    this.getTeamMes()
    // 
    this.getStudentInformation()
    // 
    this.formatDate()
  },
  // 
  methods:{
    // 
    formatDate() {
      let date = new Date();
      let year = date.getFullYear(); // 
      const month = new Date().getMonth() + 1 < 10 ? '0' + (new Date().getMonth() + 1) : (new Date().getMonth() + 1)
      const day = new Date().getDate() < 10 ? '0' + new Date().getDate() : new Date().getDate()
      let hour = date.getHours(); // 
      hour = hour < 10 ? "0" + hour : hour; // 
      let minute = date.getMinutes(); // 
      minute = minute < 10 ? "0" + minute : minute; // 
      let second = date.getSeconds(); // 
      second = second < 10 ? "0" + second : second; // 
      this.nowDate = `${year}-${month}-${day} ${hour}:${minute}:${second}`;
      console.log(this.nowDate+'!!!')
    },
    // 
    pathTo(path){
      this.$router.push({
        path: path
      })
    },
    // 
    async getTeamMes() {
      // 
      this.loading = true
      console.log("111")
      // 
      const formData = new FormData()
      formData.append('pagenum', this.queryInfo.pageNum)
      const token = window.sessionStorage.getItem('token')
      const { data: res} = await this.$axios.post('/team_getSelf', formData,
          {
            headers: {
              'accesstoken': token
            }
          })
      console.log(111)
      console.log(res)
      if(!res){
        this.$message.error('')
      }
      // 
      this.teamInfo=res
      console.log(res.length)
      console.log(this.teamInfo)
      // 
      this.total = res.length
      // 
      if(res.length === 0)
      return
      let j = 0;
      let username = this.teamInfo[j].username
      // 
      this.avatarSrc[j] = await this.getUserMes(username)
      // 
      this.loading = false
      this.$message.success('')
      this.$forceUpdate()
    },
    //,,
    async searchItem(){
      // 
      await this.getTeamMes()
    },
    //
    async getUserMes(name) {
      let result
      const token = window.sessionStorage.getItem('token')
      const formData = new FormData()
      formData.append('username', name)
      const imgData = await this.$axios.get('/getImage_user',
          {
            responseType: 'arraybuffer',
            headers: {
              'accesstoken': token
            },
            params: {
              username: name
            }
          }
      ).then(response => {
        return (
            "data:image/png;base64," +
            btoa(
                new Uint8Array(response.data).reduce(
                    (data, byte) => data + String.fromCharCode(byte),
                    ""
                )
            )
        );
      })
          .then(data => {
            result = data
          })
      return result
    },
    // () !!!!!
    async getStudentInformation(){
      console.log("!!!!!!!!!!!!!!!")
      const token = window.sessionStorage.getItem('token')
      const data = await this.$axios.get('/get_mess',
          {
            headers: {
              'accesstoken': token
            }
          }
      )
      console.log("!!!!!!!!!!!!!!!")
      console.log(data.data)
      if(data.data.username === '')
        return this.$message.error('!')
      else {
        // 
        this.teamUpForm.phone = data.data.phone;
        this.teamUpForm.qq = data.data.qq;
        this.teamUpForm.username = data.data.username;
        this.teamUpForm.sponsor = data.data.username;
        this.teamUpForm.school = data.data.school;
      }
    },
    //  ,
    async rangeChange(){
      // 
      await this.getTeamMes()
    },
    // 
    async handleCurrentChange (newPage) {
      this.queryInfo.pageNum = newPage
      console.log(this.queryInfo.pageNum)
      await this.getTeamMes()
    },
    // 
    teamUp(){
      this.postDialogVisible =true
    },
    // 
    async postTeamUp(){
      this.$refs.teamUpFormRef.validate(async valid =>{
        if(!valid) return
        // 
        const token = window.sessionStorage.getItem('token')
        const addTeamForm = new FormData()
        addTeamForm.append('title', this.teamUpForm.title)
        addTeamForm.append('sponsor', this.teamUpForm.sponsor)
        addTeamForm.append('qq', this.teamUpForm.qq)
        addTeamForm.append('phone', this.teamUpForm.phone)
        addTeamForm.append('remarks', this.teamUpForm.remarks)
        addTeamForm.append('number', this.teamUpForm.number)
        addTeamForm.append('school', this.teamUpForm.school)
        addTeamForm.append('teamType', this.teamUpForm.teamType)
        const {data:res} = await this.$axios.post('/team_insert',addTeamForm,{
          header: {
            'accesstoken': token
          }
        })
        console.log(res)
        if(!res) {
          return this.$message.error('')
        }
        //  
        this.postDialogVisible = false
        await this.getTeamMes()
        this.$message.success('!')
      })
    },
    // 
    teamUpDialogClosed(){
      this.$refs.teamUpFormRef.resetFields()
      this.teamUpForm.remarks = ''
    },
    // 
    async deleteTeam(team_id){
      // 
      this.formatDate();
      // 
      const token = window.sessionStorage.getItem('token')
      const deleteForm = new FormData()
      deleteForm.append('id', team_id)
      console.log(team_id)
      const { data: res} = await this.$axios.post('/team_reset', deleteForm,
          {
            headers: {
              'accesstoken': token
            }
          })
      console.log(res)
      if(res !== 1) {
        return this.$message.error('')
      }
      this.$message.success('')
      // 
      await this.getTeamMes()
},
  },
  name: "personal_organizeTeams"
}
</script>

<style land="less" scoped>
.yourTeam{
  width: 100%;
  height: 100%;
}
/**/
.breadCrumb{
  padding-bottom: 15px;
}
/**/
.marketMainContainer{
  display: flex;
  flex-direction: column;
  min-height: 100%;
  height: 100%;
}
/**/
.secondCard{
  padding-top: 15px;
  width: 100%;
  border-radius: 10px;
  line-height: 30px;
  padding-left: 0;
}
/**/
.teamItem{
  padding: 20px;
  height: 420px;
}
/**/
.teamCol{
  margin-bottom: 13px;
}
/**/
.el-footer {
  background-color: darkgrey;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
/**/
.teamUpDialog{
  width: 100%;
}
</style>
