<template>
  <div class = "childMainWindow">
    <!--    -->
    <el-breadcrumb separator-class="el-icon-arrow-right" style="margin-bottom: 15px">
      <el-breadcrumb-item ></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--    -->
    <div class="mainTransactionRecord">
      <el-card class="searchCard" shadow="never">
        <el-form :model="queryForm" class="el-form" ref="queryForm" inline>
          <el-form-item label="" prop="orderNo" >
            <el-input v-model="queryForm.orderNo" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="productNo">
            <el-input v-model="queryForm.productNo" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="user">
            <el-input v-model="queryForm.user" placeholder="/"></el-input>
          </el-form-item>
          <el-form-item label="" prop="orderState">
            <el-select v-model="queryForm.orderState" filterable placeholder="" clearable>
              <el-option v-for="item in orderStates" :key="item" :label="item" :value="item"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item >
            <el-button class="el-button-search" type="primary" @click="queryOrder()" style="margin-left: 40%"></el-button>
          </el-form-item>
        </el-form>
      </el-card>
      <el-card class="mainCard">
        <!--        -->
        <el-table :data="thingInfo" style="width: 100%;font-size: 14px;height: 100%" fit highlight-current-row  class="userTable" v-loading="loading" >
          <!--          -->
          <el-table-column prop="thing_id" label="#id" width="110" type="index"></el-table-column>
          <el-table-column prop="username" label="" width="110"></el-table-column>
          <el-table-column prop="thing_name" label="" width="110"></el-table-column>
          <el-table-column prop="type" label=" " width="110"></el-table-column>
          <el-table-column prop="price" label="" width="100"></el-table-column>
          <el-table-column prop="phone" label="" width="130"></el-table-column>
          <el-table-column prop="qq" label="QQ" width="120"></el-table-column>
          <el-table-column prop="die_time" label="" width="160"></el-table-column>
          <!--          -->
          <el-table-column  label="" width="120" header-align="center">
            <template slot-scope="scope"  >
              <el-tooltip  effect="dark" content="" placement="top-end" :enterable="false">
                <!--              -->
                <el-button type="primary" icon="el-icon-c-scale-to-original" size="mini"  @click="showMainContent(scope.row.remark)"></el-button>
              </el-tooltip>
              <!--              -->
              <el-button type="danger" icon="el-icon-delete" size="mini"  @click="deleteDialog(scope.row.thing_id)" ></el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-card>
      <!--        -->
      <div class="block">
        <el-pagination
            @size-change="handleSizeChange"
            @current-change="queryInfo.pageNum"
            :page-sizes="queryInfo.pageSizes"
            :page-size="queryInfo.pageSize"
            :current-page="queryInfo.pageIndex"
            layout="total, sizes, prev, pager, next, jumper"
            :total="totalMes"
            style="margin-left: 350px">
        </el-pagination>
      </div>
    </div>
    <!--    -->
    <el-dialog
        :visible.sync="seeThingDialogVisible"
        title=""
        width="30%">
      <span>{{thingRemark}}</span>
      <span slot="footer" class="dialog-footer">
      <el-button @click="seeThingDialogVisible = false"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  // 
  data () {
    return {
      // 
      searchThing: '',
      // 
      loading: false,
      // ,
      queryInfo: {
        pageNum: 1,
        orderTotal: 0, // 
        pageIndex: 1, //
        pageSize: 10, // 
        pageSizes:[5,10,30,60,100,200,400],
      },
      //
      queryForm:{
        orderNo:'',   /**/
        productNo:'', /**/
        user: '',   /**/
        orderState:'',/**/
      },
      // 
      totalMes: 100,
      // 
      thingInfo: [
      ],
      orderStates:['','','','','',''],
      // 
      thingRemark: '',
      // 
      editThingDialogVisible: false,
      // 
      deleteThingDialogVisible: false,
      // 
      seeThingDialogVisible: false
    }
  },
  // 
  created () {
    // 
    this.getItemMes()
    // 
    this.getItemNumber()
  },
  // 
  methods: {
    // queryInfo, !!!!!
    async getItemMes () {
      // 
      this.loading = true
      // 
      const formData = new FormData()
      formData.append('pagenum', this.queryInfo.pageNum)
      formData.append('keywords', this.searchThing)
      formData.append('type', this.queryInfo.itemCategory)
      formData.append('inschool', '0')
      const token = window.sessionStorage.getItem('token')

      const { data: res } = await this.$axios.post('/thing_get', formData,
          {
            headers: {
              accesstoken: token
            }
          })
      if (!res) {
        this.$message.error('')
      }
      // 
      this.thingInfo = res
      console.log(res)
      this.$forceUpdate()
      // 
      this.loading = false
      this.$message.success('')
    },
    // 
    async getItemNumber () {
      // 
      const formData = new FormData()
      formData.append('keywords', this.searchThing)
      formData.append('type', this.queryInfo.itemCategory)
      formData.append('inschool', '0')

      const token = window.sessionStorage.getItem('token')

      const { data: res } = await this.$axios.post('/thing_get_number', formData,
          {
            headers: {
              accesstoken: token
            }
          })
      if (!res && res !== 0) {
        this.totalMes = 100
        this.$message.error('')
      }
      // 
      this.totalMes = res
    },
    // 
    async handleSizeChange () {
      console.log(this.queryInfo.pageNum)
      // 
      await this.getItemNumber()
      await this.getItemMes()
    },
    // 
    showMainContent (remake) {
      this.thingRemark = remake
      this.seeThingDialogVisible = true
    },
    // 
    deleteDialog (thing_id) {
      console.log(thing_id)
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.deleteThing(thing_id)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    },
    // 
    async deleteThing (thing_id) {
      // 
      const token = window.sessionStorage.getItem('token')
      const deleteForm = new FormData()
      deleteForm.append('id', thing_id)
      console.log(thing_id)
      const { data: res } = await this.$axios.post('/thing_reset', deleteForm,
          {
            headers: {
              accesstoken: token
            }
          })
      console.log(res)
      if (res !== 1) {
        return this.$message.error('')
      }
      this.$message.success('')
      // 
      await this.getItemNumber()
      await this.getItemMes()
    },
    //
    queryOrder(){

    }
  }
}
</script>

<style land="less" scoped>
.childMainWindow{
  height: 100%;
  width: 100%;
}
.mainTransactionRecord{
  height: 100%;
}
.mainCard{
  height: 70%;
}
.searchCard{
  height: 12%;
  margin-bottom: 10px;
}
.block{
  bottom: 10px;
}

</style>
