<template>
  <div class="login_container">
    <img src="../assets/image/login.jpeg">
    <div class="login_box">
      <!--  -->
      <el-form ref="loginFormRef" :model="User" :rules="loginFormRules"  class="login_form" :label-position="labelPosition">
        <el-form-item   >
          <div>
            <img class="login_in_title" src="../assets/image/sdunft.png">
          </div>
        </el-form-item>
        <!---->
        <el-form-item  prop="username" label="/">
          <el-input v-model="User.username" class="input" placeholder=""></el-input>
        </el-form-item>
        <!---->
        <el-form-item  prop="password" label="">
          <el-input placeholder="" v-model="User.password" class="input" show-password></el-input>
        </el-form-item>
        <!---->
        <el-form-item  >
          <el-button  class="input" type="info" style="margin-top: 30px" @click="login" ></el-button>
        </el-form-item>
        <div class="registerButton" style="color: whitesmoke;padding-left: 63%;padding-top: 10px">
          ?<el-button type="text" @click="gotoRegister" style="font-size:18px"></el-button>
        </div>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  name: "login",
  data() {
    return {
      labelPosition:'top',
      User: {
        username: '111',
        password: '111'
      },
      loginFormRules: {
        username: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 10, message: ' 3  10 ', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 1, max: 16, message: ' 8  16 ', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    login () {
      const json = {
        user_id: this.User.username,
        password: this.User.password
      };

      this.$refs.loginFormRef.validate(async valid => {
        if (!valid) return this.$message.error('')
        // post
        const res = await this.$axios({
          url: '/login',
          method:  'post',
          data: json
        });
        console.log(res)
        if (res.data.msg === "" || res.data.msg===""  ) return this.$message.error('')
        this.$message.success('')
        // 1. token sessionStorage
        //  1.1API
        //  1.2tokentokensessionStorage
        // 2./home
        if(res.data.msg === "")
          await this.$router.push('/home')
        else if(res.data.msg === "")
          await this.$router.push('/managerCenter')
      })
    },
    gotoRegister () {
      this.$router.push('/register')
    }
  }
}
</script>

<style lang="less" scoped>

/deep/.el-input__suffix{
  color:#111326
}

.login_container{
  height: 100%;
  background-color: #111326;
  img {
    width: 100%;
    height: 100%;
  }
}
.login_in_title{
  color: white;
  font-size:35px;
  position: center;
  padding-left: 8%;
  padding-top: 50%;
}
.login_box {
  width: 300px;
  height: 300px;
  background-color: #111326;
  position: absolute;
  left: 30%;
  top: 60%;
  transform: translate(-50%, -50%);

    .login_form {
    position: absolute;
    bottom: 0;
    width: 100%;
    box-sizing: border-box;
  }
  .input{
    width: 350px;
  }

  .loginButton {
    display: flex;
    justify-content: flex-end;
  }
  .registerButton {
    padding-left: 150px;
  }
}
</style>
